/* SECTION TITLE */
.title-container {
    /* width: 200px;
    display: flex; */
    /* align-items: center; */
}
.block__header {
    text-align: right;
    padding-right: 3rem;
}
.block__title--container {
    position: relative;
    display: inline-block;
    margin-bottom: 1rem;
}
.block__title--content {
    font-size: 1.25rem;
    font-weight: bold;
    line-height: 2.5rem;
    color:  #2f2f2f;
    position: relative;
    padding: 0;
    margin: 0;
}
.block__title--background {
    color: #e9e9e9;
    position: absolute;
    top: 50%;
    right: 25%;
    font-weight: bold;
    font-size: 2.25rem;
    white-space: nowrap;
    padding: 0;
    margin: 0;
    transform: translate(0%, -50%);
}
@media only screen and (max-width: 382px) {
html,body{ 
        margin:0; 
        padding:0; 
        height:100%; 
        width:100%; 
        overflow: auto;
}
}
.top-layer {
    position: absolute;
    width: 100%;
    height: 100%;
    top: -100%;
    left: 0;
    bottom: auto;
    right: auto;
    background: #6c567b;
    -webkit-transition: all 0.9s cubic-bezier(0.6, 0.05, 0.4, 1);
    transition: all 0.9s cubic-bezier(0.6, 0.05, 0.4, 1);
}
.top-layer.active {
    top: 100%;
}
.top-layer--2 {
    background: #393e46;
    -webkit-transition-delay: 0.6s;
            transition-delay: 0.6s;
}
.top-layer--3 {
    background: #6c5b7b;
    -webkit-transition-delay: 0.4s;
            transition-delay: 0.4s;
}
.top-layer--4 {
    background: #c06c84;
    -webkit-transition-delay: 0.2s;
            transition-delay: 0.2s;
}
.top-layer--5 {
    background: #f67280;
    -webkit-transition-delay: 0.1s;
            transition-delay: 0.1s;
}





















